<template>
    <div class="personal-header">
      <div class="header">
        <div class="header-text">个人中心</div>
        <div class="header-icon">
          <i class="iconfont icon-setting"></i>
          <i class="iconfont icon-msg"></i>
        </div>
      </div>
      <div class="personal-pic">
        <img src="./img/per.jpg">
      </div>
      <div class="personal-login">
        <span>登录</span>
        <span>|</span>
        <span>注册</span>
      </div>
    </div>
</template>

<script>
  export default {
    name: 'PersonalHeader'
  };
</script>
<style lang="scss" scoped>
@import "~@/assets/scss/mixins";
  .personal-header{
    width: 100%;
    height: 170px;
    color: #fff;
    position: relative;
    background-color: $header-bgc-translucent;
    .header{
      width: 100%;
      font-size: $font-size-l + 2px;
      height: 40px;
      line-height: 40px;
      background-color: $header-bgc-translucent;
      &-text{
        text-align: center;
      }
      &-icon{
        position: absolute;
        right: 10px;
        top: 0;
        .iconfont{
          font-size: 20px;
        }
      }
    }
    .personal-pic{
      width: 80px;
      height: 80px;
      background-color: #fff;
      margin: 0 auto;
      border-radius: 50%;
      text-align: center;
      color: $header-bgc-translucent;
      line-height: 80px;
      img{
        vertical-align: middle;
      }
    }
    .personal-login{
      text-align: center;
      width: 100%;
      line-height: 50px;
      font-size: $font-size-l + 2px;
      span{
        margin: 0 5px;
      }
    }
  }
</style>



