<template>
    <div class="personal-orders">
      <div class="personal-orders-title">
        <i class="iconfont icon-order"></i>
        <h4 class="title">我的订单</h4>
        <span class="more">查看我的全部订单
          <i class="iconfont icon-more"></i>
        </span>
      </div>
      <div class="personal-orders-content">
        <div class="item">
          <i class="icon-pending-payment iconfont"></i>
          <p>待付款</p>
        </div>
        <div class="item">
          <i class="icon-unreceive-goods iconfont"></i>
          <p>待收货</p>
        </div>
        <div class="item">
          <i class="icon-evaluated iconfont"></i>
          <p>待评价</p>
        </div>
        <div class="item">
          <i class="icon-return-goods iconfont"></i>
          <p>退货/退款</p>
        </div>
      </div>
    </div>
</template>

<script>
  export default {
    name: 'PersonalOrders'
  };
</script>
<style lang="scss" scoped>
@import "~@/assets/scss/mixins";
  .personal-orders{
    width: 100%;
    height: 120px;
    position: relative;
    background-color: #fff;
    &-title{
      width: 100%;
      height: 40px;
      padding: 0 10px 0 40px;
      line-height: 40px;
      border-bottom: 1px solid $bgc-theme;
      .icon-order{
        font-size: 22px;
        padding-right: 10px;
        position: absolute;
        left: 10px;
      }
      .title{
        display: inline-block;
        font-size: $font-size-l;
        color: #000;
      }
      .more{
        display: inline-block;
        position: absolute;
        right: 10px;
        font-size: $font-size-base;
      }
    }

    &-content{
      @include flex-center();
      .item{
        flex: 1;
        text-align: center;
        padding-top: 10px;
        .iconfont{
          font-size: 30px;
        }
        p{
          font-size: $font-size-base;
          height: 30px;
          line-height: 30px;
        }
      }
    }
  }
</style>



