<template>
  <div class="personal-property">
    <div class="personal-property-title">
      <i class="iconfont icon-property"></i>
      <h4 class="title">我的资产</h4>
    </div>
    <div class="personal-property-content">
      <div class="item">
        <i class="icon-cash-coupon iconfont"></i>
        <p>现金券</p>
      </div>
      <div class="item">
        <i class="icon-red-envelopes iconfont"></i>
        <p>红包</p>
      </div>
      <div class="item">
        <i class="icon-balance iconfont"></i>
        <p>余额</p>
      </div>
      <div class="item">
        <i class="icon-gift-card iconfont"></i>
        <p>礼品卡</p>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'PersonalProperty'
  };
</script>
<style lang="scss" scoped>
  @import "~@/assets/scss/mixins";
  .personal-property{
    width: 100%;
    height: 120px;
    position: relative;
    margin-top: 10px;
    background-color: #fff;
    &-title{
      width: 100%;
      height: 40px;
      padding: 0 10px 0 40px;
      line-height: 40px;
      border-bottom: 1px solid $bgc-theme;
      .icon-property{
        font-size: 22px;
        padding-right: 10px;
        position: absolute;
        left: 10px;
      }
      .title{
        display: inline-block;
        font-size: $font-size-l;
        color: #000;
      }
    }

    &-content{
      @include flex-center();
      .item{
        flex: 1;
        text-align: center;
        padding-top: 10px;
        .iconfont{
          font-size: 30px;
        }
        p{
          font-size: $font-size-base;
          height: 30px;
          line-height: 30px;
        }
      }
    }
  }
</style>



