<template>
  <div class="personal">
    <div class="header-container">
      <personal-header/>
    </div>
    <lu-scroll class="content-container">
      <personal-orders/>
      <personal-property/>
      <personal-others/>
    </lu-scroll>
  </div>
</template>

<script>
  import PersonalHeader from './header.vue';
  import PersonalOrders from './orders.vue';
  import PersonalOthers from './others.vue';
  import PersonalProperty from './property.vue';
  import LuScroll from '@/base/scroll';
  export default {
    name: 'personal',
    components: {
      LuScroll,
      PersonalHeader,
      PersonalOrders,
      PersonalOthers,
      PersonalProperty
    }
  };
</script>

<style scoped lang="scss">
@import "~@/assets/scss/mixins";
  .personal{
    width: 100%;
    height: 100%;
    background-color: $bgc-theme;
    overflow: hidden;
    padding: 0 3px;
    .content-container{
      margin-top: 10px;
    }
  }
</style>



